import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { TableData, CartHeadAction } from 'components/common/index';
import { getPrizeNotes, deletePrizeNotes, exportPrizeNotes } from 'services/addPrizeNote';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import BtnExport from 'components/common/button/BtnExport';
import FormFilterScholarship from './FormFilterScholarship';

class ListScholarshipPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deletePrizeNotes(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  handleExport = async ({ studyFieldId, semesterId, yearEduId }) => {
    try {
      await exportPrizeNotes({
        studyFieldId,
        semesterId,
        yearEduId,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Ni sinh',
        dataIndex: 'pob',
        key: 'pob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'S Q',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => dateToString(record.approvalDate, 'DD/MM/YYYY'),
      },
      {
        title: 'Ni dung',
        dataIndex: 'description',
        key: 'description',
      },
    ];
    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Nhp khen thng">
        <Row>
          <CartHeadAction>
            <BtnAddNew href="#/scholarship/addForm" />
            <BtnExport
              title="Xut Excel"
              onClick={() =>
                this.handleExport({
                  studyFieldId: listParamSearch.studyFieldId,
                  semesterId: listParamSearch.semesterId,
                  yearEduId: listParamSearch.yearEduId,
                })
              }
            />
          </CartHeadAction>
          <Col span={24}>
            <FormFilterScholarship />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPrizeNotes}
              allowGetData={
                !!listParamSearch.trainTypeId &&
                !!listParamSearch.facultyId &&
                !!listParamSearch.courseId &&
                !!listParamSearch.studyFieldId &&
                !!listParamSearch.yearEduId &&
                !!listParamSearch.semesterId
              }
              paramSearch={this.state.paramSearch}
              columns={columns}
              rowKey="approvalId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListScholarshipPage />
  </Page>
);



