import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';
import './style.scss';

@Form.create({ name: 'formFilterScholarship' })
class FormFilterScholarship extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    udpateQueryParams();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate
          form={this.props.form}
          optionFieldDecorator={{
            trainTypeId: {
              rules: [requiredRule],
            },
            facultyId: {
              rules: [requiredRule],
            },
            courseId: {
              rules: [requiredRule],
            },
            studyFieldId: {
              rules: [requiredRule],
            },
          }}
        />
        <YearSemesterInput
          form={this.props.form}
          optionFieldDecorator={{
            yearEduId: {
              rules: [requiredRule],
            },
            semesterId: {
              rules: [requiredRule],
            },
          }}
        />
        <Col span={6}>
          <Form.Item label="M SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="H tn">
            {getFieldDecorator('studentFullName', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="">
            <BtnSearch htmlType="submit" />
            <BtnClear onClick={this.handleReset} />
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormFilterScholarship;



